html {
	font-size: 100%;
	box-sizing: border-box;
}

*, *::before, *::after {
	box-sizing: inherit;
}

body {
	margin: 0;
	padding: 0;
	font-size: $font-mdMob;
	font-weight: 300;
	font-family: 'Public Sans', sans-serif;
	line-height: 1.3;
	color: $grayishBlue;
	overflow-x: hidden;

	@include breakpoint-up(large) {
		font-size: $font-med;
	}
	
	&.noscroll {
		overflow: hidden;
	}
}
// Text
h1, h2, h3 {
	margin-top: 0px;
	font-weight: 300;
	color: $darkBlue;
	line-height: 1.15;
}

h1 {
		font-size: 2.31rem;
		margin-bottom: 1.5rem;

		@include breakpoint-up(large) {
			font-size: $font-xlg;
			}
		}


h2 {
	font-size: 1.875rem;
	margin-bottom: 1.5625rem;

	@include breakpoint-up(large) {
		font-size: 2.25rem;
		margin-bottom: 2.25rem;
	}
}

p {
			line-height: 1.5;
			margin-bottom: 2.25rem;
		}

a, a:visited, a:hover {
	text-decoration: none;
}

//Spacing
.container {
	
	max-width: 70rem;
	margin: 0 auto;

	&--pall {
		padding-top: 4.375rem;
		padding-right: 1.5rem;
		padding-bottom: 4.375rem;
		padding-left: 1.5rem;

		@include breakpoint-up(large) {
			padding-top: 6rem;
			padding-bottom: 6rem;
		}
	}
	
	&--py {
		padding-top: 4.375rem;
		padding-bottom: 4.375rem;

		@include breakpoint-up(large) {
			padding-top: 6rem;
			padding-bottom: 6rem;
		}
	}

	&--px {
		padding-right: 1.5rem;
		padding-left: 1.5rem;
	}
	
	&--pt {
		padding-top: 4.375rem;

		@include breakpoint-up(large) {
			padding-top: 6rem;
		}
	}

	&--pr {
		padding-right: 1.5rem;
	}

	&--pb {
		padding-bottom: 4.375rem;

		@include breakpoint-up(large) {
			padding-bottom: 6rem;
		}
	}

	&--pl {
		padding-left: 1.5rem;
	}
}
// Flexbox
.flex {
	display: flex;

	&-jc-sb {
		justify-content: space-between;
	}

	&-jc-c {
		justify-content: center;
	}

	&-ai-c {
		align-items: center;
	}
} 

// Button
button, .button {
	display: inline-block;
	cursor: pointer;
	position: relative;
	padding: 0.875rem 2.1875rem;
	border-radius: 30px;
	border: none;
	color: $white;
	font-weight: 400;
	background-image: linear-gradient(to right, $limeGreen, $brightCyan);
	font-size: $font-sm;
	transition: opacity 300ms ease-in-out; 
	overflow: hidden;

	&::before {
		content: "";
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-color: rgba(255, 255, 255, 0.25);
		opacity: 0;
		transition: opacity 150ms ease-in-out;
	}

	&:hover::before {
		opacity: 1;
	}
}

// Visibility
.hide-for-mobile {
	// hide for tablet and mobile
	@include breakpoint-down(medium){
		display: none;
	}
}

.hide-for-desktop {
	// hide for desktop viewport widths
	@include breakpoint-up(large){
		display: none;
	}
}
