.features {
	background-color: $lightGrayishBlue;
	text-align: center;

	@include breakpoint-up(medium) {
		text-align: left;
	}

	&__intro {
		margin-bottom: 3.75rem;
		
		@include breakpoint-up(large) {
			width: 60%;
		}
	}

	&__grid {

		@include breakpoint-up(medium) {
			display: flex;
			flex-wrap: wrap;
		}

	}

	&__item {
		// border: 1px solid red;
		padding: 0.9375rem;

		@include breakpoint-up(medium) {
			flex: 1 0 50%;
		}

		@include breakpoint-up(large) {
			flex: 1;
		}
	}

	&__icon {
		margin-bottom: 1.875rem;
		
		@include breakpoint-up(large) {
			margin-bottom: 2.75rem;	
		}
		

	}

	&__title {
		font-size: 1.25rem;
		margin-bottom: 1rem;
		font-weight: 300;
		color: $darkBlue;

		@include breakpoint-up(large) {
			font-size: 1.5rem;
			line-height: 1.15;
			margin-bottom: 1.5rem;
		}

	}

	&__description {
		font-size: 0.875rem;
		line-height: 1.5;

	}
}