.header {
	

	// Hamburger Open Styles
	&.open {

		.overlay {
			display: block;
		}

		.header__toggle {
			> span:first-child {
				transform: rotate(45deg);

			}

			> span:nth-child(2) {
				opacity: 0;
			}

			> span:last-child {
				transform: rotate(-45deg);
			}

		}
	}
	.overlay {
		opacity: 0;
		position: fixed;
		top: 0px;
		bottom: 0px;
		right: 0px;
		left: 0px;
		background-image: linear-gradient($darkBlue, transparent);
		z-index: 1;
	}

	nav {
		position: relative;
		background-color: $white;
		padding-top: 1.0625rem;
		padding-bottom: 1.0625rem;
		z-index: 10;
		@include breakpoint-down(large) {
			padding-left: 1.0625rem;
			padding-right: 1.0625rem;
		}

	}
	&__logo {

	img {
		width: 8.8125rem;
		height: 1.375rem;
	}
		
	}

	&__toggle { // Mobile Menu toggle

		> span {
			display: block;
			width: 26px;
			height: 2px;
			background-color: $darkBlue;
			transition: all 300ms ease-in-out;
			transform-origin: 3px 1px;

			&:not(:last-child) {
				margin-bottom: 5px;
			}
		}

		

	}
	

		&__menu {
			position: absolute;
			width: calc(100% - 3rem);
			left: 50%;
			transform: translateX(-50%);
			background-color: $white;
			margin-top: 1.5rem;
			padding: 1.625rem;
			border-radius: 4px;
			    z-index: 20;

			a {
				display: block;
				color: $darkBlue;
				padding: 0.625rem;
				text-align: center;
			}
		
		}
	
	&__links {

		a {
			font-size: $font-sm;
			color: $grayishBlue;
			transition: color 300ms ease-in-out;
			position: relative;
			
			&:not(:last-child) {
				margin-right: 32px;
			}

			&::before {
				content: "";
				display: block;
				position: absolute;
				height: 5px;
				background: linear-gradient(to right, $limeGreen, $brightCyan);
				left: 0;
				right: 0;
				bottom: -30px;
				opacity: 0;
				transition: opacity 300ms ease-in-out;
			}

			&:hover {
				color: $darkBlue;

				&::before {
					opacity: 1;
				}
			}
		}
	}
}



